import React from 'react'
import { Button } from './Button'

const list = [
	' ',
	'',
	'',
	'',
	'',
	'',
	'',
	' ..',
]

const active = ''

export function Menu() {
	return (
		<>
			{list.map((item) => {
				return <Button
					key={item}
					title = {item}
					active = {item === active}
				/>
			})}
		</>
	)
}